.vueAccordion-header{position:relative;width:100%;background:white;height:3em;letter-spacing:1px;z-index:2}.vueAccordion-header:before{content:'';display:block;height:1px;width:100%;position:absolute;top:0px;background-color:#d9d9d9}.vueAccordion-header:after{content:'';display:block;height:1px;width:100%;position:absolute;bottom:-1px;background-color:#d9d9d9}.vueAccordion-title{display:inline-block;margin-left:10px;position:relative;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.vueAccordion-icon{position:absolute;height:15px;width:15px;right:15px;top:50%;-webkit-transform:translateY(-50%);transform:translateY(-50%)}.vueAccordion-content{width:100%;overflow:hidden;height:0;-webkit-transition:height .3s ease-out;transition:height .3s ease-out}



